r\n  transition: var(--transition);\r\n}\r\n\r\n.featured-overlay {\r\n  position: absolute;\r\n  bottom: 0;\r\n  left: 0;\r\n  right: 0;\r\n  background-color: rgba(0, 0, 0, 0.7);\r\n  color: white;\r\n  padding: 15px;\r\n  /* Reduced padding */\r\n  transform: translateY(100%);\r\n  transition: transform 0.3s ease;\r\n}\r\n\r\n.featured-item:hover .featured-overlay {\r\n  transform: translateY(0);\r\n}\r\n\r\n.featured-item:hover img {\r\n  transform: scale(1.05);\r\n}\r\n\r\n.featured-overlay h3 {\r\n  margin-bottom: 10px;\r\n  font-size: 1.2rem;\r\n  /* Adjusted for smaller screens */\r\n}\r\n\r\n.featured-overlay p {\r\n  margin-bottom: 15px;\r\n  font-size: 0.8rem;\r\n  /* Adjusted for smaller screens */\r\n}\r\n\r\n.view-dish-btn {\r\n  background-color: var(--primary-color);\r\n  color: white;\r\n  border: none;\r\n  padding: 8px 16px;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  transition: var(--transition);\r\n  font-size: 0.9rem;\r\n  /* Adjusted for smaller screens */\r\n}\r\n\r\n.view-dish-btn:hover {\r\n  background-color: var(--primary-dark);\r\n}\r\n\r\n/* Reservation Modal Styles */\r\n.modal {\r\n  display: none;\r\n  position: fixed;\r\n  z-index: 2000;\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  overflow: auto;\r\n  background-color: rgba(0, 0, 0, 0.6);\r\n  backdrop-filter: blur(5px);\r\n}\r\n\r\n.modal-content {\r\n  background-color: white;\r\n  margin: 10% auto;\r\n  /* Adjusted margin for smaller screens */\r\n  padding: 20px;\r\n  /* Reduced padding */\r\n  border-radius: 10px;\r\n  box-shadow: var(--shadow);\r\n  width: 95%;\r\n  /* Full-width on small screens */\r\n  max-width: 600px;\r\n  position: relative;\r\n  animation: modalFadeIn 0.5s ease;\r\n}\r\n\r\n@keyframes modalFadeIn {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateY(-30px);\r\n  }\r\n\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n.close-modal {\r\n  position: absolute;\r\n  top: 10px;\r\n  right